'use client';

import { useState, useEffect } from "react";
import { useParams, notFound } from "next/navigation";
import { type WorkoutRoutine } from "@/lib/workouts";
import { WorkoutTracker } from "@/components/workout/WorkoutTracker";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Skeleton } from "@/components/ui/skeleton";
import { getWorkout } from "@/services/workoutService";

export default function WorkoutPage() {
  const params = useParams();
  const slug = params.slug as string;
  const [workout, setWorkout] = useState<WorkoutRoutine | null | undefined>(undefined);

  useEffect(() => {
    if (!slug) {
      setWorkout(null);
      return;
    };

    async function fetchWorkout() {
        const foundWorkout = await getWorkout(slug);
        setWorkout(foundWorkout);
    }
    
    fetchWorkout();

  }, [slug]);


  if (workout === undefined) {
     return (
        <div className="flex flex-col items-center justify-center min-h-screen p-4 sm:p-6">
            <Card className="w-full max-w-md mx-auto">
                <CardHeader>
                    <Skeleton className="h-8 w-3/4 mx-auto" />
                    <Skeleton className="h-4 w-1/2 mx-auto" />
                </CardHeader>
                <CardContent className="flex flex-col items-center space-y-6">
                    <div className="grid grid-cols-2 gap-4 text-center my-6 w-full">
                        <div>
                            <Skeleton className="h-4 w-12 mx-auto mb-2" />
                            <Skeleton className="h-8 w-16 mx-auto" />
                        </div>
                        <div>
                            <Skeleton className="h-4 w-12 mx-auto mb-2" />
                            <Skeleton className="h-8 w-24 mx-auto" />
                        </div>
                    </div>
                    <Skeleton className="h-14 w-full rounded-full" />
                </CardContent>
            </Card>
        </div>
    );
  }

  if (workout === null) {
    notFound();
  }

  return <WorkoutTracker routine={workout} />;
}
